body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 230px;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-image {
  max-width: 90%;
  max-height: 90%;
}

.close-modal {
  color: #fff;
  cursor: pointer;
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  top: 10px;
  right: 10px;
}

